import template from './faq-module-index.html.twig';

Shopware.Component.register('faq-module-index', {
    template,

    inject: [
        'repositoryFactory',
        'acl' // Stellen Sie sicher, dass 'acl' benötigt wird, entfernen Sie es sonst
    ],

    data() {
        return {
            question: '',
            answer: '',
            isLoading: false,
            faqRepository: null,
            productRepository: [],
            successMessage: '',
            errorMessage: '',
            mainProducts: [],
            selectedProductId: null
        };
    },

    computed: {
        mainProductsOptions() {
            return this.mainProducts.map(product => {
                return { id: product.id, name: product.name };
            });
        }
    },

    created() {
        this.productRepository = this.repositoryFactory.create('product');
        this.loadMainProducts();
        this.faqRepository = this.repositoryFactory.create('faq_entity');
    },
    
    

    methods: {
        loadMainProducts() {
            this.isLoading = true;
            const criteria = new Shopware.Data.Criteria();
            criteria.addFilter(Shopware.Data.Criteria.equals('product.parentId', null));
    
            this.productRepository.search(criteria, Shopware.Context.api)
                .then(result => {
                    this.mainProducts = result;
                    this.isLoading = false;
                })
                .catch(error => {
                    console.error('Fehler beim Laden der Hauptprodukte:', error);
                    this.isLoading = false;
                });
        },

        async saveFAQ() {
            if (!this.selectedProductId || !this.question || !this.answer) {
                this.errorMessage = 'Bitte füllen Sie alle Felder aus.';
                setTimeout(() => { this.errorMessage = ''; }, 5000);
                return;
            }
        
            const faqData = {
                productId: this.selectedProductId,
                question: this.question,
                answer: this.answer
            };
        
            try {
                await this.faqRepository.save(faqData, Shopware.Context.api);
                this.successMessage = 'FAQ wurde gespeichert';
                setTimeout(() => { this.successMessage = ''; }, 5000);
            } catch (error) {
                console.error('Fehler beim Speichern der FAQ:', error);
                this.errorMessage = `Fehler beim Speichern der FAQ: ${error.message}`;
                setTimeout(() => { this.errorMessage = ''; }, 5000);
            }
        }
    }
});
